@import "../../../styles/vars";
@import "../../../styles/mixins";


.careerDescription {
  padding-bottom: 100px;

  @include lgScreenMedia {
    padding-bottom: 80px;
  }

  @include mdScreenMedia {
    padding-bottom: 60px;
  }

  @include smScreenMedia {
    padding-bottom: 40px;
  }

  &__title {
    @include setDistance(margin-bottom,60);

    @include smScreenMedia {
      text-align: center;
    }
  }

  &__contentText {
    max-width: 693px;
    margin-bottom: 15px;
  }
}
